import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import '@fortawesome/fontawesome-free/css/all.min.css';

function Home({ loginedUser }) {
    const [detailData, setDetailData] = useState({});
    const [isLiked, setIsLiked] = useState(false);
    const [likeCount, setLikeCount] = useState(0);
    const { id } = useParams(); // URL에서 id 파라미터를 가져옴

    useEffect(() => {
        if (id) {
            // 게시물 데이터 가져오기
            axios.get(`http://portfoliokjm.cafe24app.com/api/oneBoardGet/${id}`, {
                headers: {
                    'Content-Type': 'application/json',
                },
            })
            .then(response => {
                setDetailData(response.data);
            })
            .catch(error => {
                console.error(error);
            });

            // 좋아요 상태와 개수 가져오기
            if (loginedUser.user) {
                axios.post(`http://portfoliokjm.cafe24app.com/api/isLiked`, {
                    userIndex: loginedUser.user.index,
                    boardIndex: id,
                })
                .then(response => {
                    setIsLiked(response.data.isLiked);
                })
                .catch(error => {
                    console.error(error);
                });
            }

            axios.get(`http://portfoliokjm.cafe24app.com/api/likes/${id}`)
            .then(response => {
                setLikeCount(response.data.likeCount);
            })
            .catch(error => {
                console.error(error);
            });
        }
    }, [id, loginedUser.user]);

    const handleLike = () => {
        if (!loginedUser.user) {
            alert('로그인 후 이용 가능합니다.');
            return;
        }

        const api = isLiked ? 'unlike' : 'like';
        axios.post(`http://portfoliokjm.cafe24app.com/api/${api}`, {
            userIndex: loginedUser.user.index,
            boardIndex: id,
        })
        .then(() => {
            setIsLiked(!isLiked);
            setLikeCount(prevCount => isLiked ? prevCount - 1 : prevCount + 1);
        })
        .catch(error => {
            console.error(error);
        });
    };

    return (
        <div className='page detail'>
            <div className='container'>
                <div className='detailWrap'>
                    <div className='detailTitle'>
                        <div className='key'>
                            제목
                        </div>
                        <div className='value'>
                            {detailData.title}
                        </div>
                    </div>
                    <pre className='detailContent'>
                        {detailData.content}
                    </pre>
                    <div className='likeSection'>
                        <button onClick={handleLike} className={isLiked ? 'like' : ''}>
                            <i class="fas fa-heart"></i>
                            {likeCount}
                        </button>
                    </div>
                    <div className='detailInfo'>
                        <div className='detailInfoItem'>
                            <div className='key'>
                                게시글 번호
                            </div>
                            <div className='value'>
                                {detailData.index}
                            </div>
                        </div>
                        <div className='detailInfoItem'>
                            <div className='key'>
                                작성일
                            </div>
                            <div className='value'>
                                {detailData.date}
                            </div>
                        </div>
                        <div className='detailInfoItem'>
                            <div className='key'>
                                작성자
                            </div>
                            <div className='value'>
                                {detailData.writerName}
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    );
}

export default Home;
