import React, { useEffect, useState } from 'react';
import './App.css';
import { HashRouter as Router, Route, Routes, useNavigate } from 'react-router-dom';
import Header from './components/Header';
import Home from './pages/Home';
import Write from './pages/Write';
import Detail from './pages/Detail';
import MyPage from './pages/MyPage';
import Login from './pages/Login';
import PopularBoard from './pages/PopularBoard';
import Search from './pages/Search';
import ErrorPage from './pages/ErrorPage';
import cookie from 'react-cookies';

function App() {
    const [loginedUser, setLoginedUser] = useState({});
    const navigate = useNavigate();

    useEffect(() => {
        // 쿠키에서 로그인 정보 가져오기
        const getLoginedUserInfo = () => {
            const userCookie = cookie.load('loginedUser');
            if (userCookie && Object.keys(userCookie).length > 0) {
                setLoginedUser(userCookie); // 쿠키에서 가져온 데이터를 그대로 사용
            } else {
                setLoginedUser({});
                navigate('/login');
            }
        };

        getLoginedUserInfo();
    }, [navigate]);

    return (
        <div className={`frame ${loginedUser.user && loginedUser.user.darkTheme ? 'dark' : 'light'}`}>
            <Header loginedUser={loginedUser} />
            <Routes>
                <Route path="/" element={<Home loginedUser={loginedUser} />} />
                <Route path="/detail/:id" element={<Detail loginedUser={loginedUser} />} />
                <Route path="/login" element={<Login />} />
                <Route path="/write" element={<Write loginedUser={loginedUser}/>} />
                <Route path="/myPage" element={<MyPage loginedUser={loginedUser}/>} />
                <Route path="/popularBoard" element={<PopularBoard />} />
                <Route path="/Search" element={<Search />} />
                <Route path="/*" element={<ErrorPage/>} />
            </Routes>
        </div>
    );
}

function AppWrapper() {
    return (
        <Router>
            <App />
        </Router>
    );
}

export default AppWrapper;
