import React,{useState,useEffect} from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import Table from './../components/Table';

function Home() {
    const [totalCount,setTotalCount] = useState(0);
    const [boardData,setBoardData] = useState([]);

    useEffect(()=>{
        axios.get('http://portfoliokjm.cafe24app.com/api/boardTotalCount', {
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(response => {
            setTotalCount(response.data);
            
        })
        .catch(error => {
            
        });
        axios.get('http://portfoliokjm.cafe24app.com/api/getAllBoard', {
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(response => {
            setBoardData(response.data);
        })
        .catch(error => {
            console.error(error);
        });
    },[]);

    return (
        <div className='page'>
            <div className='visual'>
                <div className='container'>
                    <div className='texts'>
                        <div className='title'>
                            ThinkShare
                        </div>
                        <div className='mainTypo'>
                            우리의 생각을 나누는 공간.
                        </div>
                    </div>
                </div>
            </div>
            <div className='container'>
                <div className='boardWrap'>
                    <div className='title'>
                        게시판
                        <Link to="/write">
                            글쓰기
                        </Link>
                    </div>
                    <Table totalCount={totalCount} boardData={boardData}/>
                </div>
            </div>
        </div>
    );
}
export default Home;