import React,{useState,useEffect} from 'react';
import { Link } from 'react-router-dom';

function ErrorPage() {
    return (
        <div className='page errorPage'>
            <div className='container'>
                <div className='title'>
                    유효하지 않은 주소입니다.
                </div>
                <Link to="/">
                    홈으로
                </Link>
            </div>
        </div>
    );
}
export default ErrorPage;