import React, { useEffect, useState } from 'react';
import '@fortawesome/fontawesome-free/css/all.min.css';
import Table from './../components/Table';
import axios from 'axios';

function MyPage({ loginedUser }) {
    const [maskedPassword, setMaskedPassword] = useState('');
    const [boardData, setBoardData] = useState([]);
    const [totalCount, setTotalCount] = useState(0);

    useEffect(() => {
        if (loginedUser && loginedUser.user && loginedUser.user.pw) {
            setMaskedPassword('*'.repeat(loginedUser.user.pw.length));
        }

        if (loginedUser && loginedUser.user) {
            // 사용자 작성 게시글을 가져오는 API 호출
            axios.get(`/api/getPostsByUser/${loginedUser.user.index}`)
                .then(response => {
                    setBoardData(response.data);
                    setTotalCount(response.data.length); // 게시글 총 개수 설정
                })
                .catch(error => {
                    console.error('Error fetching user posts: ', error);
                });
        }
    }, [loginedUser]);

    // loginedUser와 loginedUser.user가 정의되어 있는지 확인
    if (!loginedUser || !loginedUser.user) {
        return <div className='page loading'>Loading...</div>;
    }

    return (
        <div className='page myPage'>
            <div className='container'>
                <div className='myPageWrap'>
                    <div className='title'>
                        유저 정보
                    </div>
                    <div className='item'>
                        <div className='key'>
                            닉네임
                        </div>
                        <div className='value'>
                            {loginedUser.user.name}
                        </div>
                    </div>
                    <div className='item'>
                        <div className='key'>
                            ID
                        </div>
                        <div className='value'>
                            {loginedUser.user.id}
                        </div>
                    </div>
                    <div className='item'>
                        <div className='key'>
                            PW
                        </div>
                        <div className='value'>
                            {maskedPassword}
                        </div>
                    </div>
                    <div className='item'>
                        <div className='key'>
                            유저 번호
                        </div>
                        <div className='value'>
                            {loginedUser.user.index}
                        </div>
                    </div>
                </div>
                <div className='writeBoard'>
                    <div className='title'>
                        작성한 게시글
                    </div>
                    <div className='boardWrap'>
                        <Table totalCount={totalCount} boardData={boardData} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default MyPage;
