import React, { useState, useEffect } from 'react';
import '@fortawesome/fontawesome-free/css/all.min.css';
import { HashRouter as Router, Route, Routes, useNavigate } from 'react-router-dom';

function Table({ totalCount, boardData }) {
    const [currentPage, setCurrentPage] = useState(0);
    const [pageGroup, setPageGroup] = useState(1);
    const [totalPageCount, setTotalPageCount] = useState(1);
    const navigate = useNavigate();

    const dateTrans = (date) => {
        return date ? date.substring(0, 10) : ''; // 9번째 문자 이후의 문자열을 제거
    };

    useEffect(() => {
        const count = Math.ceil(totalCount / 10);
        setTotalPageCount(count);
    }, [totalCount]);

    const handlePrevClick = () => {
        if (currentPage > 0) {
            setCurrentPage(currentPage - 1);
        }
    };

    const handleNextClick = () => {
        if ((currentPage + 1) * 10 < totalCount) {
            setCurrentPage(currentPage + 1);
        }
    };

    const handlePageGroupPrevClick = () => {
        if (pageGroup > 1) {
            setPageGroup(pageGroup - 1);
            setCurrentPage((pageGroup - 2) * 10);
        }
    };

    const handlePageGroupNextClick = () => {
        if (pageGroup * 10 < totalPageCount) {
            setPageGroup(pageGroup + 1);
            setCurrentPage(pageGroup * 10);
        }
    };

    const moveDetail = (boardIndex) => {
        navigate(`/detail/${boardIndex}`);
    }

    const startIdx = currentPage * 10;
    const endIdx = Math.min(startIdx + 10, totalCount);
    const startPage = (pageGroup - 1) * 10;
    const endPage = Math.min(startPage + 10, totalPageCount);

    return (
        <div className='page'>
            <div className='totalCount'>
                총 {totalCount}개
            </div>
            <div className='tableWrapper'>
                <table className='boardList'>
                    <colgroup>
                        <col width="120px" />
                        <col width="auto" />
                        <col width="200px" />
                        <col width="200px" />
                        <col width="200px" />
                    </colgroup>
                    <thead>
                        <tr>
                            <th>No</th>
                            <th className='titleTh'>제목</th>
                            <th>글쓴이</th>
                            <th>작성일</th>
                            <th>추천</th>
                        </tr>
                    </thead>
                    <tbody>
                        {boardData.length > 0 ? (
                            boardData.slice(startIdx, endIdx).map((board, index) => (
                                <tr key={index} onClick={() => { moveDetail(board.index) }}>
                                    <td>{board.index}</td>
                                    <td className='titleTd'>{board.title}</td>
                                    <td>{board.writerName}</td>
                                    <td>{dateTrans(board.date)}</td>
                                    <td>{board.likeCount}</td>
                                </tr>
                            ))
                        ) : (
                            <tr style={{height:"430px"}}>
                                <td colSpan="5">조회된 게시글이 없습니다.</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
            <div className='pagination'>
                <button className='prevPage' onClick={handlePageGroupPrevClick} disabled={pageGroup === 1}>
                    <i className="fas fa-chevron-left"></i>
                </button>
                {Array.from({ length: endPage - startPage }, (_, index) => {
                    const pageIndex = startPage + index;
                    return (
                        <button
                            type='button'
                            key={pageIndex}
                            onClick={() => setCurrentPage(pageIndex)}
                            className={currentPage === pageIndex ? 'active' : ''}
                        >
                            {pageIndex + 1}
                        </button>
                    );
                })}
                <button className='nextPage' onClick={handlePageGroupNextClick} disabled={endPage >= totalPageCount}>
                    <i className="fas fa-chevron-right"></i>
                </button>
            </div>
        </div>
    );
}

export default Table;
