import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Table from './../components/Table';

function Search({ loginedUser }) {
    const [searchResult, setSearchResult] = useState([]);
    const [totalCount, setTotalCount] = useState(0);
    const [searchTerm, setSearchTerm] = useState('');
    const [searchFilter, setSearchFilter] = useState('제목');
    const [searchResultKeyword, setSearchResultKeyword] = useState('');

    const handleSearchClick = async () => {
        if(!searchTerm){
            alert("검색어를 입력해주세요.");
            return;
        }
        try {
            const response = await axios.get('/api/search', {
                params: {
                    filter: searchFilter,
                    term: searchTerm,
                },
            });
            setSearchResultKeyword(searchTerm);
            setSearchResult(response.data.results);
            setTotalCount(response.data.totalCount);
        } catch (error) {
            console.error('검색 요청 중 오류 발생:', error);
        }
    };

    const handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            handleSearchClick();
        }
    };

    return (
        <div className='page search'>
            <div className='container'>
                <div className='title'>
                    검색하기
                </div>
                <div className='searchBarWrap'>
                    <select value={searchFilter} onChange={(e) => setSearchFilter(e.target.value)}>
                        <option value="제목">제목</option>
                        <option value="내용">내용</option>
                        <option value="작성자">작성자</option>
                    </select>
                    <input 
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        onKeyPress={handleKeyPress} // 엔터 키를 눌렀을 때 검색
                    />
                    <button type='button' onClick={handleSearchClick}>
                        <i className="fas fa-search"></i>
                    </button>
                </div>
                <div className='searchResult'>
                    <div className='title'>
                        검색어 '{searchResultKeyword}'의 대한 내용입니다.
                    </div>
                    <div className='boardWrap'>
                        <Table totalCount={totalCount} boardData={searchResult} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Search;
