import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import cookie from 'react-cookies';

function Header({ loginedUser }) {
    const initialDarkTheme = loginedUser && loginedUser.user && loginedUser.user.darkTheme;
    const [darkTheme, setDarkTheme] = useState(initialDarkTheme || false);

    useEffect(() => {
        if (loginedUser && loginedUser.user && loginedUser.user.darkTheme !== undefined) {
            setDarkTheme(loginedUser.user.darkTheme);
        }
    }, [loginedUser]);

    const handleThemeChange = () => {
        const newTheme = !darkTheme;
        setDarkTheme(newTheme);

        // 테마 변경 시 쿠키에 저장
        const updatedUser = { ...loginedUser.user, darkTheme: newTheme };
        cookie.save('loginedUser', { user: updatedUser }, { path: '/' });
        window.location.reload();
    };

    const logout = () => {
        cookie.remove('loginedUser', { path: '/' });
        window.location.href = '/notice-board/';
    };

    return (
        <header>
            <div className='container headerInner'>
                <div>
                    <Link to="/" className='logo'>
                        TkS
                    </Link>
                    <Link to="/">
                        게시글 목록
                    </Link>
                    <Link to="/popularBoard">
                        인기 게시글
                    </Link>
                    <Link to="/search">
                        검색
                    </Link>
                </div>
                {
                    loginedUser && Object.keys(loginedUser).length > 0 && (
                        <div>
                            <div className='themeToggle'>
                                <input 
                                    type='checkbox' 
                                    className='themeInput' 
                                    checked={darkTheme}
                                    onChange={handleThemeChange}
                                />
                            </div>
                            <Link to="/myPage">
                                My Page
                            </Link>
                            <div className='logoutBtn' onClick={logout}>
                                로그아웃
                            </div>
                        </div>
                    )
                }
            </div>
        </header>
    );
}

export default Header;
