import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import cookie from 'react-cookies';

function Login() {
    const [id, setId] = useState('guest');
    const [pw, setPw] = useState('guest1234');
    const navigate = useNavigate();

    const loginPost = () => {
        if (id.trim() === '') {
            alert("아이디를 입력해 주세요.");
            return;
        }
        if (pw.trim() === '') {
            alert("비밀번호를 입력해 주세요.");
            return;
        }

        axios.post('http://portfoliokjm.cafe24app.com/api/noticeboard-login', { id, pw }, {
            headers: {
                'Content-Type': 'application/json'
            },
            withCredentials: true // 세션을 유지하기 위해 쿠키를 포함하도록 설정
        })
        .then(response => {
            // 로그인 성공 시 서버에서 받은 데이터를 쿠키에 저장
            const expires = new Date();
            expires.setMinutes(expires.getMinutes() + 60); // 쿠키 만료 시간 설정 (예: 60분 후)

            // 예시로 받은 데이터를 전체 저장하려면 response.data를 직렬화해서 저장하는 것이 좋습니다.
            cookie.save('loginedUser', JSON.stringify(response.data), {
                path: '/notice-board/', // 쿠키 경로 설정
                expires, // 쿠키 만료 시간 설정
                // secure: true, // HTTPS 프로토콜에서만 전송하도록 설정 (필요 시)
                // httpOnly: true // HTTP 요청에서만 접근 가능하도록 설정 (필요 시)
            });

            // alert("로그인 되었습니다.");
            navigate('/notice-board/');
        })
        .catch(error => {
            console.error("로그인 오류:", error);
            alert("아이디 또는 비밀번호가 일치하지 않습니다.");
        });
    };

    return (
        <div className='page login'>
            <div className='container'>
                <div className='loginInner'>
                    <div className='left'>
                        <p className='text'>
                            Welcome to FreeBoard!
                        </p>
                    </div>
                    <div className='right'>
                        <div className='loginText'>
                            로그인
                        </div>
                        <input
                            type="text"
                            placeholder='Id'
                            autoFocus
                            value={id}
                            onChange={(e) => setId(e.target.value)}
                        />
                        <input
                            type="password"
                            placeholder='Pw'
                            value={pw}
                            onChange={(e) => setPw(e.target.value)}
                        />
                        <button type='button' className='loginBtn' onClick={loginPost}>
                            LOGIN
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Login;
