import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

function Write({ loginedUser }) {
    const navigate = useNavigate();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');

    const calcleClickHandler = () => {
        navigate('/notice-board/');
    };

    const submitClickHandler = async () => {
        if (!title || !content) {
            alert("제목과 내용을 모두 입력해주세요.");
            return;
        }

        try {
            const response = await axios.post('http://portfoliokjm.cafe24app.com/api/createBoard', {
                title,
                content,
                writerIndex: loginedUser.user.index // Assuming loginedUser contains user info with index
            });

            if (response.data.message === 'Board created successfully') {
                alert("게시글이 성공적으로 작성되었습니다.");
                navigate('/notice-board/');
            }
        } catch (error) {
            console.error('Error creating board:', error);
            alert('게시글 작성 중 오류가 발생했습니다.');
        }
    };

    return (
        <div className='page write'>
            <div className='container'>
                <div className='writeWrap'>
                    <div className='item'>
                        <div className='key'>
                            제목                        
                        </div>
                        <div className='value'>
                            <input 
                                type="text"
                                value={title}
                                onChange={(e) => setTitle(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className='item'>
                        <div className='key'>
                            내용
                        </div>
                        <div className='value'>
                            <textarea 
                                value={content}
                                onChange={(e) => setContent(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className='btnWrap'>
                        <button type='button' className='submit' onClick={submitClickHandler}>작성</button>
                        <button type='button' className='cancle' onClick={calcleClickHandler}>취소</button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Write;
